import React from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import Home from './component/Home';
import About from './component/About';
import Portofolio from './component/Portofolio';  // Fixed spelling of "Portfolio"
import Clients from './component/Clients';
import HomeLayout from './component/HomeLayout';  // Layout for the shared Header & Footer
import './App.css';

function App() {
  return (
    <BrowserRouter basename="/ahad-profile"> {/* Add the correct basename */}
      <Routes>
        <Route path="/" element={<HomeLayout />}>
          <Route index element={<Home />} />  {/* Home Route */}
          <Route path="about" element={<About />} />
          <Route path="portfolio" element={<Portofolio />} />
          <Route path="clients" element={<Clients />} />
        </Route>
      </Routes>
    </BrowserRouter>
  );
}
export default App;
