import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPhoneAlt, faBars, faTimes } from '@fortawesome/free-solid-svg-icons';

function Header() {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

  useEffect(() => {
    // Update window width on resize
    const handleResize = () => setWindowWidth(window.innerWidth);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Toggle menu on click
  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  // Close the menu when a link is clicked
  const closeMenu = () => {
    if (isMenuOpen) {
      setIsMenuOpen(false);
    }
  };

  return (
    <header>
      <div className="container">
        <Link to="/" className="logo" onClick={closeMenu}>
          <img src="./Ahad-logo.png" alt="Logo" />
        </Link>
        <div className="navbar-toggle" onClick={toggleMenu}>
          {isMenuOpen ? (
            <FontAwesomeIcon icon={faTimes} size="lg" />
          ) : (
            <FontAwesomeIcon icon={faBars} size="lg" />
          )}
        </div>
        <nav className={`navbar ${isMenuOpen || windowWidth > 768 ? 'open' : ''}`}>
          <Link to="/" className="navlinks" onClick={closeMenu}>Home</Link>
          <Link to="/about" className="navlinks" onClick={closeMenu}>About</Link>
          <Link to="/portfolio" className="navlinks" onClick={closeMenu}>Portfolio</Link>
          <Link to="/clients" className="navlinks" onClick={closeMenu}>Clients</Link>
        </nav>

        {/* Contact Button */}
        <a
          href="https://wa.me/923355686335"
          className="contact-btn"
          target="_blank"
          rel="noopener noreferrer"
        >
          <FontAwesomeIcon icon={faPhoneAlt} /> Contact me
        </a>
      </div>
    </header>
  );
}

export default Header;
